.contactsPage {
  max-width: 80%;
    margin: 0 auto;
    padding: 20px;
  }
  
  .contactsPage header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .contactsPage header h1 {
    font-size: 2.5rem;
    color: orangered;
  }
  
  .contactsPage .contact-info, .contactsPage .contact-form {
    margin-bottom: 40px;
  }
  
  .contactsPage .contact-info h2, .contactsPage .contact-form h2 {
    font-size: 1.5rem; 
    color: orangered;
  }
  
  .contactsPage .contact-info p {
    color: #B0BDD3;
    margin-bottom: 10px;
  }
  
  .contactsPage .contact-form form {
    display: grid;
    grid-gap: 10px;
  }
  
  .contactsPage .contact-form label {
    font-weight: bold;
    color: #B0BDD3; 
  }
  
  .contactsPage .contact-form input, .contactsPage .contact-form textarea {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #B0BDD3;
    color: #B0BDD3; 
  }
  
  .contactsPage .contact-form button {
    padding: 10px 20px;
    background-color: orangered;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contactsPage .contact-form button:hover {
    background-color: chocolate;
  }
